<template>
  <div class="modules">
    <component
    :key="index"
    v-for="(mod, index) in countArray"
    class="module-container"
    :is="getModule(mod)">
  </component>
  </div>

</template>

<script>
import manager from '@/assets/js/storageManager.js'
import {map} from '@/assets/js/moduleIdMap.js'

export default {
  name: 'Modules',
  props: ['config'],
  data () {
    return {
      modules: []
    }
  },
  methods: {
    generateCountArray (n) {
      // Creates an array where every element is either null or a component
      const arr = [];
      for (let i = 0; i < n; i++) {
        for (let j = 0; j < this.modules.length; j++) {
          if (this.modules[j].modulePos === i) {
            arr.push(map[this.modules[j].moduleId]);
          } else {
            arr.push(null);
          }
        }
      }
      return arr;
    },
    getModule (mod) {
      if (mod) {
        return mod;
      }

      return map['2'];
    }
  },
  computed: {
    countArray () {
      return this.generateCountArray(this.config.numPanels);
    }
  },
  created () {
    this.numPanels = this.config.numPanels;
    this.modules = manager.getModules();
  }
}

</script>

<style scoped>
  div.modules {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .module-container {
    /* background-color: #535964; */
    border: 1px solid #535964;
    min-width: 31vw;
    min-height: 40vh;
    margin: 10px;
  }
</style>



